}
.info{
    font-size:large;
    text-align: justify;
    font-family: sans-serif;
}

.tech{
    text-align: center;
}
.image{
    margin-left: 10%;
    align-items: center;
    -webkit-animation: mover 2s infinite  alternate;
    animation: mover 2s infinite  alternate;
    
}
@-webkit-keyframes mover {
    0% { transform: translateY(0); }
    100% { transform: translateY(-20px); }
}
@keyframes mover {
    0% { transform: translateY(0); }
    100% { transform: translateY(-20px); }
}
.certification{
    text-align: center;
    font-size:x-large; 
}
.certify{
    margin-left: 48%;
    -webkit-animation: mover 2s infinite  alternate;
    animation: mover 2s infinite  alternate;

}
.grid-container {
    display: grid;
    grid-template-columns: auto auto auto;
    /* background-color: #d1af68; */
    padding: 5px;
    margin: 50px;
  }
.grid-item {
    background-color:bisque;
    border: 1px solid rgb(4, 4, 5);
    border-radius: 10px;
    padding: 20px;
    margin-right: 10px;
    font-size: 20px;
    text-align: center;
  }
.project{
    text-align: justify;
}

.footer {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 2rem;
    background-color: rgb(241, 177, 97);
  }
  
  .footer a {
    display: inline-block;
    margin: 0 1rem;
    font-size: 1.5rem;
    color: #060101;
    padding-top: 10px;
  
  }
  
  .footer a:hover {
    color: #0a0add;
  }
  